.reservation {
  font-family: "Montez", cursive;
  text-align: center;
  font-weight: 400;
  font-size: 3rem;
  color: darkgoldenrod;
}
.error {
  border: 2px solid #FF6565;
}
.error-message {
  color: #FF6565;
    text-align: left;
    margin-left:30px;
    margin-bottom:20px;
    padding:  .2em;
    height: 1em;
    margin-top:-25px;
  position: relative;
  font-size: .8em;
}
.book-table {
  font-family: "Suranna", serif;
  text-align: center;
  font-size: 2rem;
}

.icon {
  margin-left: -5%;
}

.form-head {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.formPageContainer {
  text-align: left;
}

.image1 {
  display: flex;
  
  flex-direction: column;
  margin-top: 50%;
}
.img-fluid{
  
  display: flex;
  
  flex-direction: column;
  margin-top: 60%;

}
@media only screen and (max-width: 768px) {
  .image1{
    display: none;
  }
  .img-fluid{
    display: none;
  }
 }
 @media only screen and (max-width: 425px) {
  .image1{
    display: none;
  }
  .img-fluid{
    display: none;
  }
 }

 @media only screen and (max-width: 375px) {
  .image1{
    display: none;
  }
  .img-fluid{
    display: none;
  }
 }
 @media only screen and (max-width: 320px) {
  .image1{
    display: none;
  }
  .img-fluid{
    display: none;
  }
 }
