.imgAbout {
  width: 100%;
  height: 70%;
  margin-top: 0%;
}
.imagee{
  max-width: 100%;
  height: auto;
}

.imgAboutOur{
  width: 100%;
  height: 70%;
  margin-top: 30%;
  display: inline;
}
.imgAboutOur:hover {
  border: 1px solid #777;
}

.customHeaderContainer {
  text-align: center;
}

.customMainHeader {
  color: #b48c80;
  font-size: 36px;
  font-family: "Montez", cursive;
}

.customSubHeader {
  color: #161d27;
  font-family: "Suranna", serif;
  font-size: 2rem;
  text-transform: uppercase;
}

.aboutquote {
  width: 80%;
  margin: 0 auto 20px auto;
  color: #b48c80;
}
.aboutquote p {
  font-family: "Suranna", serif;
  font-size: 36px;
  line-height: 42px;
  padding-bottom: 10px;
  letter-spacing: 2px;
}
.blockquoteText {
  letter-spacing: 1px;
  font-size: 16px;
}
.author {
  color: #b48c80;
}
.nmt{
  margin-top: -30px;
}

.aboutSecondCover {
  background-image: url(bkg-img-2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  position: relative;
}
.aboutSecondHeading-container {
  text-align: center;
  position: absolute;
  top: 40%;
  right: 20%;
}
.aboutSecondHeadingSecondTitle {
  font-size: 50px;
  color: #fff;
  font-family: "Suranna", serif;
  text-transform: uppercase;
  line-height: 60px;
  letter-spacing: 2px;
}

.aboutSecondHeadingFirstTitle {
  font-size: 55px;
  color: #fcda9a;
  font-family: "Montez", cursive;
  text-transform: none;
}

.findTable {
  font-family: "Suranna", serif;
  text-transform: uppercase;
  font-size: 21px;
  border-bottom: 1px solid;
  color: #161d27;
  display: inline-block;
  padding-bottom: 3px;
  text-align: center;
  cursor: pointer;
}
.findTable:hover {
  color: #b48c80;
}
.aboutThirdCover {
  background-image: url(bkg-img-3.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  position: relative;
}
.aboutThirdHeading-container {
  text-align: center;
  position: absolute;
  top: 40%;
  right: 8%;
}
.aboutThirdHeadingSecondTitle {
  font-size: 50px;
  color: #fff;
  font-family: "Suranna", serif;
  text-transform: uppercase;
  line-height: 60px;
  letter-spacing: 2px;
}

.aboutThirdHeadingFirstTitle {
  font-size: 55px;
  color: #fcda9a;
  font-family: "Montez", cursive;
  text-transform: none;
}
.memberTeam {
  text-align: center;
  line-height: 22px;
}
.memberName {
  font-family: "Suranna", serif;
  color: #161d27;
  margin-bottom: 5px;
  font-size: 27px;
  text-transform: uppercase;
}
.memberPosition {
  padding-bottom: 0;
  color: #fd9090;
}

.memberAbout {
  text-align: center;
  line-height: 22px;
  color: #1f0606;
  font-family: "Montserrat-UltraLight", Arial, sans-serif;
}
.aboutPageContent {
  height: 100vh;
  width: 100%;
  background: url(bkg-img-4.jpg) center/cover no-repeat border-box;

  position: relative;
}
.caurosalContainer {
  width:100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 1046px) {
  .aboutHeadingFirstTitle,
  .aboutSecondHeadingFirstTitle,
  .aboutThirdHeadingFirstTitle {
    font-size: 45px;
  }
  .aboutHeadingSecondTitle,
  .aboutSecondHeadingSecondTitle,
  .aboutThirdHeadingSecondTitle {
    font-size: 35px;
  }
  
}
@media only screen and (max-width: 850px) {
  .aboutHeadingFirstTitle,
  .aboutSecondHeadingFirstTitle,
  .aboutThirdHeadingFirstTitle {
    font-size: 35px;
  }
  .aboutHeadingSecondTitle,
  .aboutSecondHeadingSecondTitle,
  .aboutThirdHeadingSecondTitle {
    font-size: 24px;
  }
}



