.display-table {
  display: table;
  height: 100%;
  width: 100%;
}

.display-table-cell {
  display: table-cell;
  vertical-align: middle;
}

.font-white {
  color: #fff;
}

.ComingSoon {
  position: relative;
  text-align: center;
  height: 100vh;
  z-index: 1;
  padding: 0 20px;
  background-size: cover;
  color: #fff;
  overflow:hidden;
  font-family: "Open Sans", sans-serif;
  background-image: url(../../assets/images/home5.jpg);
}

.ComingSoon:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  opacity: 0.2;
  background: #000;
}
.contentContainer{
  padding: 42px;
}
.ComingSoon .title {
  font-size: 3.5em;
  line-height: 1;
  font-weight: 500;
  letter-spacing: 2px;
  
}

.ComingSoon .subTitle{
  font-size: 25px;
  padding: 30px;
}
.ComingSoon .desc {
  margin: 20px auto;
  max-width: 500px;
  line-height: 1.6;
  font-size: 22px;
  padding: 46px;
  font-weight: 400;
}
.units {
  padding: 25px;
  font-size: 22px;
}
.counting {
  display: inline-flex;
}
.countHour,
.countMin,
.countSec,
.countDays {
  padding: 17px;
  background: black;
  margin: 8px;
  font-size: 29px;
}
@media (max-width:875px)
{
.ComingSoon .title {
  font-size: 3em;
}
.units {
  padding: 9px;
  font-size: 20px;
}

.countHour, .countMin, .countSec, .countDays {
  padding: 6px;
  margin: 5px;
  font-size: 22px;
}
.ComingSoon .subTitle {
  font-size: 19px;
}
}
@media (max-width: 612px)
{
.ComingSoon .title {
    font-size: 2em;
}
.ComingSoon .subTitle {
  font-size: 15px;
}
.units {
  padding: 9px;
  font-size: 15px;
}
.countHour, .countMin, .countSec, .countDays {
  padding: 3px;
  margin: 5px;
  font-size: 22px;
  background: none;
}

}

@media (max-width:490px){
  .ComingSoon .title{
    font-size: 1.5em;
  }
  .ComingSoon .subTitle {
    font-size: 12px;
}
  .contentContainer{
    padding: 0;
  }
  .units {
    padding: 7px;
    font-size: 14px;
}
.countHour, .countMin, .countSec, .countDays {
  padding: 2px;
  margin: 4px;
  font-size: 19px;
}
}

@media (max-width: 360px){
  .countHour, .countMin, .countSec, .countDays {
    padding: 2px;
    margin: 0px;
    font-size: 18px;
    
}
.counting{
  margin-left: -15px;
}
.units {
  padding: 7px;
  font-size: 14px;
}
}




