.visitus-container {
  height: 100vh;
  position: relative;
}

.header .heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.visit-us {
  font-family: "Montez", cursive;
  text-align: center;
  font-weight: 400;
  font-size: 2.5rem;
  color: darkgoldenrod;
  margin-block-end: 0%;
}

.header {
  margin-bottom: 5%;
  margin-top: 3%;
  text-align: center;
}
.location {
  font-family: "Suranna", serif;
  text-align: center;
  font-size: 2rem;
  color: black;
}
.design {
  color: goldenrod;
  font-size: smaller;
  margin-top: 0%;
}
.location-header {
  margin-left: 0%;
  margin-top: -6%;
  color: goldenrod;
  font-weight: 1500;
  font-size: 30px;
}
.header-circle {
  font-weight: 900;
  font-size: 20px;
  color: goldenrod;
}
.sub-header {
  font-family: "Suranna", serif;
  text-align: center;
  font-size: 22px;
  color: rgb(94, 90, 90);
}
.content {
  font-family: "Suranna", serif;
  text-align: center;
  font-size: 1.3rem;
  color: darkgray;
  margin-bottom: 5%;
}
.first-col {
  font-family: "Suranna", serif;
  text-align: center;
  font-size: 30px;
}
.second-col {
  font-family: "Suranna", serif;
  text-align: center;
  font-size: 30px;
}
.direction {
  font-family: "Suranna", serif;
  text-align: center;
  font-size: 20px;
  text-decoration: underline;
  color: rgb(71, 63, 63);
  display: flex;
  align-items: center;
  justify-content: center;
}
.direction span {
  font-family: "Suranna", serif;
}

@media screen and (max-width: 480px) {
  .first-col {
    margin-bottom: 2rem;
  }
  .location{
    font-size:1.5rem;
  }
}
